import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Card, Row, Col } from 'antd';
import { CartHeadAction } from 'components/common';
// import { dateToString } from 'utils/formatUtil';
import BtnExport from 'components/common/button/BtnExport';
import { exportAcademicStatistics } from 'services/studentForAcademicStatistics';
import { getQueryParams } from 'utils/urlUtil';
import FormSearch from './FormSearch';
import './style.scss';

class ReportAcademicStatisticsPage extends Component {
  state = { paramSearch: {} };
  // handleDelete = async id => {
  //   await deleteDiscipline(id);
  //   message.success('Xa thnh cng');
  //   this.setState({ reload: this.state.reload + 1 });
  // };

  componentDidMount = () => {
    this.handleChangeQueryParams();
  };

  componentWillReceiveProps = () => {
    this.handleChangeQueryParams();
  };

  handleChangeQueryParams = () => {
    this.setState({ paramSearch: getQueryParams() });
  };

  handleExport = async ({
    trainTypeId,
    facultyId,
    courseId,
    studyFieldId,
    classId,
    yearEduId,
    semesterId,
  }) => {
    try {
      await exportAcademicStatistics({
        trainTypeId,
        facultyId,
        courseId,
        studyFieldId,
        classId,
        yearEduId,
        semesterId,
      });
    } catch (error) {
      console.log(error);
    }
  };

  render() {
    // const { reload } = this.state;
    // const columns = [
    //   {
    //     title: 'S quyt nh',
    //     dataIndex: 'approvalNumber',
    //     key: 'approvalNumber',
    //   },
    //   {
    //     title: 'Ngy quyt nh',
    //     dataIndex: 'approvalDate',
    //     render: (text, record) => dateToString(record.approvalDate, 'DD/MM/YYYY'),
    //   },
    //   {
    //     title: 'Ni dung',
    //     dataIndex: 'description',
    //     key: 'description',
    //   },
    //   {
    //     title: 'Thao tc',
    //     dataIndex: 'operation',
    //     render: (text, record) => (
    //       <ColEditDelete
    //         hrefEdit={`#/reportDisciplines/form/edit/${record.id}`}
    //         onConfirmDelete={() => this.handleDelete(record.id)}
    //       />
    //     ),
    //   },
    // ];

    const listParamSearch = this.state.paramSearch;
    return (
      <Card title="Bo co qu trnh k lut">
        <CartHeadAction>
          <BtnExport
            title="Xut Excel"
            onClick={() =>
              this.handleExport({
                trainTypeId: listParamSearch.trainTypeId,
                facultyId: listParamSearch.facultyId,
                courseId: listParamSearch.courseId,
                studyFieldId: listParamSearch.studyFieldId,
                classId: listParamSearch.classId,
                yearEduId: listParamSearch.yearEduId,
                semesterId: listParamSearch.semesterId,
              })
            }
          />
        </CartHeadAction>
        <Row>
          <Col span={24}>
            <FormSearch />
          </Col>
        </Row>
        {/* <Row>
          <Col span={24}>
            <CartHeadAction>
              <BtnAddNew href="#/reportDisciplines/form/new" />
            </CartHeadAction>
          </Col>
        </Row> */}
        {/* <Row>
          <Col span={24}>
            <TableData
              reload={reload}
              service={getReportDisciplines}
              columns={columns}
              rowKey="id"
            />
          </Col>
        </Row> */}
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <ReportAcademicStatisticsPage />
  </Page>
);



